
html,body{margin:0;padding:0
}
#app{font-family:'Avenir',Helvetica,Arial,sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-align:center;color:#2c3e50
}
#app header{width:100%;padding:30px 0;margin-bottom:20px
}
#app header .title{font-size:46px;line-height:20px;color:#48B884
}
#app header p{color:#7F8C8D
}
#app header p .button{color:#48B884
}
#app .content{display:-ms-flexbox;display:flex;width:800px;height:650px;margin:0 auto
}
#app .content>div{-ms-flex:1;flex:1;height:100%
}
#app .content .preview{background:url("~assets/iphone.png") center center no-repeat;background-size:324px 642px;position:relative
}
#app .content .preview .preview-content{width:256px;height:464px;position:absolute;left:72px;top:91px;background:#fff;overflow-y:scroll
}
#app .content .setting span{color:#48B884
}


